import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [

  {
    path: '/set-params',
    name: 'set-params',
    meta: {layout: 'empty'},
    component: () => import('../components/SetParams')
  },
  {
    path: '/login',
    name: 'login',
    meta: {layout: 'empty'},
    component: () => import('../views/Login')
  },
  {
    path: '/register',
    name: 'register',
    meta: {layout: 'empty'},
    component: () => import('../views/Register')
  },
  {
    path: '/your-data',
    name: 'your-data',
    meta: {layout: 'main'},
    component: () => import('../views/YourData')
  },
  {
    path: '/main',
    name: 'Main',
    meta: {layout: 'main'},
    component: () => import('../views/Main')
  },
  {
    path: '/your-weight',
    name: 'your-weight',
    meta: {layout: 'main'},
    component: () => import('../components/ChangeData')
  },{
    path: '/profile',
    name: 'profile',
    meta: {layout: 'main'},
    component: () => import('../views/Profile')
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
