<template>
  <div class="navigation">
    <div class="navigation__name">
      <span>{{this.$store.state.name}}</span>
    </div>
    <div class="navigation__menu" v-if="screen > 576">
      <router-link class="navigation__menu_item" to="/your-data">Главная</router-link>
      <router-link class="navigation__menu_item" to="/your-weight">Дневник</router-link>
      <router-link class="navigation__menu_item" to="/profile">Личный кабинет</router-link>
      <span class="navigation__menu_item" @click="logout">Выйти</span>
    </div>
    <div class="navigation__menu_small" v-else>
      <span class="menu_btn" @click="show = !show">MENU</span>
      <div class="navigation__menu_small__pos" :class="{menu_show: show === true}" @click="show = false">
        <router-link class="navigation__menu_small_item" to="/your-data">Главная</router-link>
        <router-link class="navigation__menu_small_item" to="/your-weight">Дневник</router-link>
        <router-link class="navigation__menu_small_item" to="/profile">Личный кабинет</router-link>
        <span class="navigation__menu_small_item" @click="logout">Выйти</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Nav",
  data: () => ({
    name: '',
    show: false,
    screen: null
  }),
  computed: {},
  methods: {
    async logout() {
      await this.$store.dispatch('logout')
      this.$router.push('/login')
    }
  },
  async mounted() {
    this.screen = screen.width
    await this.$store.dispatch('getValueFromDatabase')
    await this.$store.dispatch('getName')
  }
}
</script>

