import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Paginate from 'vuejs-paginate'
import dateFilter from "./filters/date.filter";
import Loader from "./components/Loader/Loader";
import Vuelidate from "vuelidate/src";
import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

Vue.config.productionTip = false
Vue.use(Vuelidate)
Vue.filter('date', dateFilter)
Vue.component('Loader', Loader)
Vue.component('Paginate', Paginate)


firebase.initializeApp({
  apiKey: "AIzaSyC8TlCH2VJXLN1ieTQCK8QUkUfLgLnZ2No",
  authDomain: "weightcontrol-e0fb9.firebaseapp.com",
  databaseURL: "https://weightcontrol-e0fb9.firebaseio.com",
  projectId: "weightcontrol-e0fb9",
  storageBucket: "weightcontrol-e0fb9.appspot.com",
  messagingSenderId: "34960063845",
  appId: "1:34960063845:web:988f4dd3e1937ddb9ab58c"
})

let app

firebase.auth().onAuthStateChanged(() => {
  if(!app) {
    app = new Vue({
      router,
      store,
      render: h => h(App)
    }).$mount('#app')
  }
})

