import Vue from 'vue'
import Vuex from 'vuex'
import auth from './auth'
import firebase from "firebase/app";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentData: [],
    name: ''
  },
  mutations: {
    setCurrentData: (state, payload) => {
      state.currentData = payload
    },
    setName: (state, payload) => {
      state.name = payload
    },
    clearData: state => state.currentData = {}
  },
  actions: {
    async setValueToDatabase({dispatch}, {date, time, day, weight, height, wishWeight}) {
      try {
        const uid = await dispatch('getUid')
        const record = await firebase.database().ref(`/users/${uid}/data`).push()
        await record.set({
          date,
          time,
          day,
          weight,
          id: record.key
        })

        await firebase.database().ref(`/users/${uid}/other-data/height`).set({
          height
        })
        await firebase.database().ref(`/users/${uid}/other-data/wishWeight`).set({
          wishWeight
        })
      } catch (e) {}
    },
    async deleteRecord({dispatch}, id) {
      const uid = await dispatch('getUid')
      await firebase.database().ref(`/users/${uid}/data/${id}`).remove()
          .then(function () {
        alert('Запись удалена')
          })
          .catch(function(error) {
        alert("Remove failed: " + error.message)
          })
    },
    async deleteDatabase({dispatch}) {
      const uid = await dispatch('getUid')
      await firebase.database().ref(`/users/${uid}/data/`).remove()
          .then(function () {
        alert('Записи дневника успешно удалены')
          })
          .catch(function(error) {
        alert("Remove failed: " + error.message)
          })
      const record = await firebase.database().ref(`/users/${uid}/data`).push()
      await record.set({
        date: '00.00.0000',
        time: '00:00:00',
        day: 'ноль',
        weight: '000',
        id: record.key
      })
    },
    async getValueFromDatabase({dispatch}) {
      try{
        const uid = await dispatch('getUid')
        const valueDB = (await firebase.database().ref(`/users/${uid}/data`).once("value")).val() || {}
        return  Object.keys(valueDB).map(key => ({...valueDB[key]}))
      } catch (e) {}
    },
    async getOtherFromDatabase({dispatch}) {
      try{
        const uid = await dispatch('getUid')
        const valueDB = (await firebase.database().ref(`/users/${uid}/other-data`).once("value")).val() || {}
        return  Object.keys(valueDB).map(key => ({...valueDB[key]}))
      } catch (e) {}
    },
    async getName({commit, dispatch}) {
      try{
        const uid = await dispatch('getUid')
        const valueDB = (await firebase.database().ref(`/users/${uid}/info`).once("value")).val() || {}
        commit('setName', valueDB.name)
        return valueDB.name

      } catch (e) {}
    },
    async addValueToDatabase({dispatch}, params) {
      const uid = await dispatch('getUid')
      await firebase.database().ref(`/users/${uid}/data`).push().set({
        params
      })
    }
  },
  getters: {
    GET_DATA: state => state.currentData,
    GET_NAME: state => state.name
  },
  modules: {
    auth
  }
})
